	module.exports = {"data":{"allDataJson":{"edges":[{"node":{"bmjsiteHead":[{"id":"34866726","accountId":"5351042","webPropertyId":"UA-5351042-34","name":"bmjopen.bmj.com - Reporting","websiteUrl":"http://bmjopen.bmj.com"}],"bmjsiteData":[{"pagePath":"/content/8/3/e019438","pageTitle":"Effect of pasta in the context of low-glycaemic index dietary patterns on body weight and markers of adiposity: a systematic review and meta-analysis of randomised controlled trials in adults | BMJ Open","pageviews":"2153"},{"pagePath":"/content/4/9/e005055","pageTitle":"The role of hospital managers in quality and patient safety: a systematic review | BMJ Open","pageviews":"1016"},{"pagePath":"/content/5/11/e008052.full","pageTitle":"Bicycling injury hospitalisation rates in Canadian jurisdictions: analyses examining associations with helmet legislation and mode share | BMJ Open","pageviews":"686"},{"pagePath":"/content/5/9/e007118","pageTitle":"The effect of statins on average survival in randomised trials, an analysis of end point postponement | BMJ Open","pageviews":"623"},{"pagePath":"/content/6/6/e010401","pageTitle":"Lack of an association or an inverse association between low-density-lipoprotein cholesterol and mortality in the elderly: a systematic review | BMJ Open","pageviews":"502"},{"pagePath":"/content/7/5/e015233","pageTitle":"Long-term antibiotics for prevention of recurrent urinary tract infection in older adults: systematic review and meta-analysis of randomised trials | BMJ Open","pageviews":"495"},{"pagePath":"/content/8/3/e021312.full","pageTitle":"Trends, geographical variation and factors associated with prescribing of gluten-free foods in English primary care: a cross-sectional study | BMJ Open","pageviews":"482"},{"pagePath":"/content/1/2/e000154","pageTitle":"Condom use during commercial sex among clients of Hijra sex workers in Karachi, Pakistan (cross-sectional study) | BMJ Open","pageviews":"385"},{"pagePath":"/content/7/5/e013778","pageTitle":"Reducing social isolation and loneliness in older people: a systematic review protocol | BMJ Open","pageviews":"373"},{"pagePath":"/content/8/4/e021076","pageTitle":"Behaviours preceding suicides at railway and underground locations: a multimethodological qualitative approach | BMJ Open","pageviews":"373"}]}}]}},"pathContext":{"group":[{"node":{"id":"af35f47b-ca9d-583b-b33d-5a70a3243917","slug":"editors-picks-2015-part-seven","title":"Editors’ Picks 2015: Part Seven","excerpt":"<p>Polypharmacy and potentially inappropriate prescription of medicines This week we have been looking at some of our staff editors’ favourite articles from 2015. These papers have covered a variety of important issues in medical research, from the transparency of clinical trials to the publication of negative findings. Our final editors’ pick takes us to Ireland, [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/10/editors-picks-2015-part-seven/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 10, 2016","template":"","format":"standard"}},{"node":{"id":"55764dbd-a637-5ce2-b118-f1ba6d78b43d","slug":"editors-picks-2015-part-six","title":"Editors’ Picks 2015: Part Six","excerpt":"<p>The 2D:4D digit ratio and autism risk: is there an association? In December we surveyed our staff editors to tell us about their favourite articles from 2015. Our penultimate editors&#8217; pick investigates the relationship between autism risk and a putative marker of testosterone exposure: the 2D: 4D digit ratio. In spite of large investments in [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/09/editors-picks-2015-part-six/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 09, 2016","template":"","format":"standard"}},{"node":{"id":"be080966-c8aa-5c09-96a0-ae5ddafc1781","slug":"editors-picks-2015-part-five","title":"Editors’ Picks 2015: Part Five","excerpt":"<p>How well reported are interventions in systematic reviews? In December we surveyed our staff editors to tell us about their favourite articles from 2015. Part 5 focuses on another important editorial issue: the quality of reporting in systematic reviews. Clinicians and healthcare professionals often rely on systematic reviews and meta-analyses for information on how effective interventions [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/08/editors-picks-2015-part-five/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 08, 2016","template":"","format":"standard"}},{"node":{"id":"1b39b744-c37c-5e10-bf0a-8b111e33d7dd","slug":"editors-picks-2015-part-four","title":"Editors’ Picks 2015: Part Four","excerpt":"<p>Long-term health effects of exercise for the elderly: study protocol Each day this week we&#8217;ve been looking at a published paper from 2015 that sparked the interest of one of our staff editors. Our fourth pick is a study protocol for a randomised controlled trial of exercise training in an elderly population. BMJ Open supports [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/07/editors-picks-2015-part-four/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 07, 2016","template":"","format":"standard"}},{"node":{"id":"880ec165-d53c-542b-965f-62d7fbdb1aba","slug":"antidepressants-and-risk-of-mania-medically-unexplained-symptoms-and-the-association-between-alcohol-and-mortality-in-alzheimers-most-read-articles-in-december","title":"Antidepressants and risk of mania, medically unexplained symptoms and the association between alcohol and mortality in Alzheimer&#8217;s: Most read articles in December","excerpt":"<p>Antidepressants, medically unexplained symptoms, and alcohol consumption in Alzheimer&#8217;s patients December saw a lot of new papers entering the Top 10 Most Read, with half of them being from the current issue. Patel et al&#8216;s research paper, complete with video abstract and press release, questions whether antidepressants increase the risk of mania and bipolar disorder [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/06/antidepressants-and-risk-of-mania-medically-unexplained-symptoms-and-the-association-between-alcohol-and-mortality-in-alzheimers-most-read-articles-in-december/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 06, 2016","template":"","format":"standard"}},{"node":{"id":"30b7f9bf-4f55-5060-9612-7577ad59c384","slug":"editors-picks-2015-part-three","title":"Editors’ Picks 2015: Part Three","excerpt":"<p>Combating Dissemination Bias in Clinical Research: Recommendations Tip of the iceberg: Dissemination bias includes publication bias, where published papers that are exposed to readers are biased in favour of particular characteristics (e.g. positive findings). Papers with other characteristics (e.g. negative findings) are not published and, as a result, remain out of view from readers. &nbsp; [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/06/editors-picks-2015-part-three/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 06, 2016","template":"","format":"standard"}},{"node":{"id":"68f911b9-e2d0-5532-9bb8-ce555a88211f","slug":"editors-picks-2015-part-two","title":"Editors&#8217; Picks 2015: Part Two","excerpt":"<p>Clinical Trial Transparency: disclosures for new drugs below ethical and legal standards In December we surveyed our staff editors, who oversee and manage the peer review process, to tell us about their favourite articles from 2015. Part 2 looks at clinical trial transparency for new drugs.  The ability of medical and healthcare professionals to select [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/05/editors-picks-2015-part-two/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 05, 2016","template":"","format":"standard"}},{"node":{"id":"690a8032-698a-5329-8e7e-b06baedf6503","slug":"editors-picks-2015-part-one","title":"Editors&#8217; Picks 2015: Part One","excerpt":"<p>2015 was another exciting year for BMJ Open, with over 1500 articles published. Since launching in 2011, we have published over 4000 articles, and we look forward to February, when the journal will celebrate its fifth anniversary. In December we surveyed our staff editors, who oversee and manage the peer review process, to tell us about [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/04/editors-picks-2015-part-one/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 04, 2016","template":"","format":"standard"}},{"node":{"id":"c6585335-c427-57ad-8080-6241f063c9c5","slug":"the-importance-of-reviewers-2015","title":"The importance of reviewers – 2015","excerpt":"<p>The peer review process is central to scholarly research, a critical part of the publishing process and a method of quality control for the scientific community. While peer review can seem like a daunting, never-ending task at times, without it journals would not survive and continue to publish the important, accurate findings they do today. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/12/23/the-importance-of-reviewers-2015/\">Read More&#8230;</a></p>\n","status":"publish","date":"December 23, 2015","template":"","format":"standard"}},{"node":{"id":"6ed409ca-55cf-57dd-8631-520c43fe2eca","slug":"certain-antidepressants-linked-to-heightened-risk-of-mania-and-bipolar-disorder","title":"Certain antidepressants linked to heightened risk of mania and bipolar disorder","excerpt":"<p>Strongest association found for SSRIs and venlafaxine Taking certain antidepressants for depression is linked to a heightened risk of subsequent mania and bipolar disorder, reveals research published in the online journal BMJ Open. The strongest association seemed to be for serotonin reuptake inhibitors, or SSRIs for short, and the dual action antidepressant venlafaxine, the analysis [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/12/16/certain-antidepressants-linked-to-heightened-risk-of-mania-and-bipolar-disorder/\">Read More&#8230;</a></p>\n","status":"publish","date":"December 16, 2015","template":"","format":"standard"}}],"pathPrefix":"","first":false,"last":false,"index":10,"pageCount":27,"additionalContext":{}}}

